.product-container {
    width: 85%;
    margin: auto;
}

.product-card {
    display: flex;
    flex-wrap: wrap;
}

.product-cards {
    padding: 5px;
    width: 16.6%;
}

.product-button {
    border: 1px solid #ccc;
}

.product-button:hover {
    border: 1px solid #ccc;
    box-shadow: 2px 2px 10px 3px #ccc;
}

.product-card img {
    width: 100%;
    cursor: pointer;
}

.product-container h2 {
    font-size: 24px;
    color: rgb(68, 68, 68);
}

.snack-store .product-cards {
    width: 25% !important;
}


/* responsive */

@media only screen and (min-width:320px),
(min-width:200px) {
    .product-cards {
        padding: 5px;
        width: 32.6%;
    }

    .snack-store .product-cards {
        width: 50% !important;
    }


}

@media only screen and (min-width:768px) {
    .product-cards {
        padding: 5px;
        width: 16.6%;
    }

    .snack-store .product-cards {
        width: 25% !important;
    }


}

@media only screen and (min-width:1024px),
(min-width:900px) {
    .product-cards {
        padding: 5px;
        width: 16.6%;
    }

    .snack-store .product-cards {
        width: 25% !important;
    }


}