.form-container{
    padding: 200px 0px;
    width:100%;
    background:linear-gradient(rgba(70, 69, 69, 0.5), rgb(70, 69, 69, 0.5)), url("https://c4.wallpaperflare.com/wallpaper/642/840/7/wheat-greens-lemon-mushrooms-wallpaper-preview.jpg");
    background-repeat: no-repeat;
    height: 140vh;
    width: 100%;
    background-size: 100% 100%;
}
.feeddiv{
    width: 60%;
    text-align: center;
    padding:30px;
    margin: auto;
    border-radius:10px;
}

.input:focus {
    outline: none;
}

.input::placeholder {
    color: #b3b2b2;
}

.input{
    width: 50%;
    border: 1px solid #ccc;
    padding: 7px;
    border-radius: 5px;
    
}
.span{
    width: 50%;
    background-color: #fff;
    font-size: 18px;
    font-weight: bold;
}
.head{
    margin: 20px 0px;
    color: #fff;
    font-family:'Roboto';
    text-align:center;
}
.feeddiv form div label {
    width: 15%;
    margin: 10px;
    color:#fff;
}
.text{
    font-family: 'Roboto';
    font-size: large;
}
.btn{
    margin-top: 4% !important;
    border-radius: 2em !important;
}

.paragraph{
  box-shadow: 2px 2px 10px #ccc;
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;
  margin: 10px 0px;
}



