*,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
}

.header-section {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: white;
    z-index: 3;
    font-family: 'Roboto';
}

.header-container {
    width: 85%;
    margin: auto;
    padding: 10px 0px;
}

.header {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
}

.header-image {
    width: 20%;
    padding-left: 10px;
}

.header-image img {
    width: 72%;
}

.header-image .logo {
    width: 20%;
    border-radius: 8px;
    box-shadow: 0px 0px 3px grey;
}

.a-tag:hover {
    color: #6DD139;
}

.header-content {
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
}

.header-contact {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.header-contact-content {
    display: flex;
    padding: 0px 5px;
}

.heaer-contact-icon {
    padding: 0px 10px;
}

.heaer-contact-icon a {
    font-size: 13px;
    text-decoration: none;
    color: black;
}

.heaer-contact-icon i {
    font-size: 13px;
    padding-right: 5px;
}

.header-search {
    width: 99%;
    padding: 10px 0px 0px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.header-input {
    width: 70%;
    position: relative;
    /* height: 100%; */
}

.header-input input {
    width: 80%;
    padding: 11px 10px;
    height: 100%;
    border: 1px solid #888;
}

.header-input input:focus {
    outline: none;
    border: 1px solid #84c225;
}

.header-input button {
    background-color: #84c225;
    color: white;
    padding: 11px;
    font-size: 20px;
    border: none;
    position: absolute;
    top: 0;
    right: 93px;
    line-height: 0;
}

.header-input button i {
    font-weight: 100;
}


.header-buy {
    width: 17%;
    background-color: #f2f2f2;
    padding: 13px;
    display: flex;
    position: relative;
}

.header-buy i {
    color: #da251d;
    font-size: 32px;
}

.header-buy .header-icon-content {
    padding-left: 20px;
    color: #4d4d4d;
    font-weight: 500;
}

.header-icon-content p {
    font-size: 13px;
    color: #666;
    margin: 0;
}

.basket {
    position: absolute;
    right: 0;
    top: 100%;
    background-color: #f2f2f2;
    width: 550px;
    border: 1px solid lightgrey;
    padding: 5px;
    z-index: 1;
}

.basket-content {
    overflow-y: auto;
    max-height: 270px;
}

.basket-items {
    padding: 5px;
}

.basket-item {
    padding: 5px;
    background-color: white;
    display: flex;
}

.basket-image {
    width: 13%;
    border: 1px solid #f2f2f2;
}

.basket-image img {
    width: 100%;
}

.basket-item-info {
    width: 45%;
    padding: 0px 10px;
}

.basket-item-info h6 {
    font-weight: 100;
    color: #666;
}

.basket-item-info p {
    font-size: 13px;
    padding: 5px 0px;
}

.basket-item-info span {
    color: gray;
    font-size: 10px;
}

.basket-content-info {
    width: 43%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.basket-item-count {
    display: flex;
    justify-content: center;
    align-items: center;
}

.basket-item-button {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    border: 1px solid #ccc;
    background-color: white;
    font-weight: 400;
    font-size: 1.0rem;
    cursor: pointer;
    height: 1.5rem;
    width: 1.5rem;
    color: #9cc63d;

}

.basket-item-button:hover {
    background-color: #9cc63d;
    color: #fff;
}

.basket-item-count div {
    padding: 0px 8px;
}



.baske-rate p {
    margin-bottom: -1px;
    text-align: center;
    font-size: 14px;
    color: #666;
}

.baske-rate span {
    color: #6DD139;
    font-size: 10px;
}



.basket-remove p {
    color: grey;
    cursor: pointer;
}


.basket-bill {
    display: flex;
    width: 97%;
}

.basket-bill-left {
    width: 50%;
    padding: 5px;
}

.basket-bill-left p {
    font-size: 10.5px;
    background-color: white;
    padding: 5px;
    text-align: center;
    color: #666;
}

.basket-bill-left p i {
    font-size: 10px;
    color: #6DD139;
}


.basket-bill-right {
    width: 50%;
    padding: 5px;
}

.basket-bill-content {
    background-color: white;
    padding: 5px 10px;
}

.basket-bill-content div {
    display: flex;
    justify-content: space-between;
    padding: 2px 0px;
}

.basket-bill-content div p {
    font-size: 13px;
    color: #666;
}

.basket-bill-button button {
    cursor: pointer;
    width: 100%;
    background-color: #6DD139;
    color: white;
    border: none;
    padding: 5px 0px;
    font-size: 14px;
}



.no-products {
    padding: 30px 0px;
    text-align: center;
    background-color: white;
    margin: 5px;
    color: #999;
    font-size: 14px;
}



.header-bottom {
    display: flex;
    flex-wrap: nowrap;
}

.header-bottom-dropdwon {
    padding: 10px;
    width: 15%;
    background-color: #84c225;
}

.header-bottom-dropdwon p {
    font-size: 15px;
    color: white;
    margin: 0;
}


.header-bottom-offer {
    width: 81.5%;
    padding: 5px 10px;
    display: flex;
    align-items: center;
    border: 1px solid lightgrey;
}

.css-i4bv87-MuiSvgIcon-root {
    font-size: 18px !important;
    color: hsl(0, 5%, 71%);
    width: 1em !important;
    height: 1em !important;
    margin: 0px !important;
}

.header-bottom-offer .localoff .css-vubbuv{
    font-size: 18px !important;
    color: #da251d !important;
    width: 1em !important;
    height: 1em !important;
    margin: 0px !important;
}

.header-bottom-offer p {
    padding-left: 5px;
    font-size: 13px;
    margin: 0;
}

.css-wb57ya-MuiFormControl-root-MuiTextField-root {
    width: 100% !important;
}

/* responsive */

@media only screen and (min-width:320px),
(min-width:200px) {
    .header-buy {
        width: 41%;
    }

    .header-input button {
        right: 26px !important;
        font-size: 9px;
    }

    .header-input {
        width: 60%;
    }

    .header-input input {
        padding: 5px 10px;
    }

    .header-bottom-dropdwon {
        width: 55%;
    }

    .heaer-contact-icon a {
        font-size: 5px;
    }

    .header-bottom-dropdwon p {
        font-size: 9px;
    }

    .header-icon-content p {
        font-size: 9px;
    }

    .header-buy i {
        color: #da251d;
        font-size: 13px;
    }

    .header-buy .header-icon-content {
        padding-left: 5px;
    }

    .basket {
        width: 300px;
    }

    /* .basket-item-button {
        font-size: .6rem;
        height: 1rem;
        width: 1rem;
    } */

    .basket-item-info {
        width: 40%;
    }

    .basket-item-count div {
        padding: 0px 3px;
    }

    .basket-item-count div .qua-btn {
        font-size: 0.6rem !important;
    }

    .baske-rate span {
        font-size: 8px !important;
    }

    .baske-rate p,
    .basket-bill-content div p,
    .basket-bill-button button {
        font-size: 10px !important;
    }

    .header-image .logo {
        width: 60%;
    }
}

@media only screen and (min-width:768px) {
    .header-buy {
        width: 29% !important;
    }

    .header-input button {
        right: 72px !important;
        font-size: 20px;
    }

    .header-input {
        width: 70%;
    }

    .header-input input {
        padding: 11px 10px;
    }

    .header-bottom-dropdwon {
        width: 29%;
    }

    .heaer-contact-icon a {
        font-size: 13px;
    }

    .header-bottom-dropdwon p {
        font-size: 15px
    }

    .header-icon-content p {
        font-size: 13px;
    }

    .header-buy i {
        color: #da251d;
        font-size: 32px;
    }

    .header-buy .header-icon-content {
        padding-left: 20px;
    }

    .basket {
        width: 550px;
    }

    /* .basket-item-button {
        font-size: 1.5rem;
        height: 2.5rem;
        width: 2.5rem;
    } */

    .basket-item-info {
        width: 43%;
    }

    .basket-item-count div {
        padding: 0px 8px;
    }

    .basket-item-count div .qua-btn {
        font-size: 1.2rem !important;
    }

    .baske-rate span {
        font-size: 10px !important;
    }

    .baske-rate p,
    .basket-bill-content div p,
    .basket-bill-button button {
        font-size: 14px !important;
    }

    .header-image .logo {
        width: 40%;
    }
}

@media only screen and (min-width:1024px),
(min-width:900px) {
    .header-buy {
        width: 22% !important;
    }

    .header-input button {
        right: 90px !important;
        font-size: 21px;
    }

    .header-input {
        width: 70%;
    }

    .header-input input {
        padding: 11px 10px;
    }

    .header-bottom-dropdwon {
        width: 15%;
    }

    .heaer-contact-icon a {
        font-size: 13px;
    }

    .header-bottom-dropdwon p {
        font-size: 15px
    }

    .header-icon-content p {
        font-size: 13px;
    }

    .header-buy i {
        color: #da251d;
        font-size: 32px;
    }

    .header-buy .header-icon-content {
        padding-left: 20px;
    }

    .basket {
        width: 550px;
    }

    /* .basket-item-button {
        font-size: 1.5rem;
        height: 2.5rem;
        width: 2.5rem;
    } */

    .basket-item-info {
        width: 43%;
    }

    .basket-item-count div {
        padding: 0px 8px;
    }

    .basket-item-count div .qua-btn {
        font-size: 1.2rem !important;
    }

    .baske-rate span {
        font-size: 10px !important;
    }

    .baske-rate p,
    .basket-bill-content div p,
    .basket-bill-button button {
        font-size: 14px !important;
    }

    .header-image .logo {
        width: 30%;
    }
}