.details-container {
    width: 85%;
    margin: auto;
    padding-top: 160px;
}

.details {
    display: flex;
    flex-wrap: wrap;
}

.details-image {
    width: 40%;
    padding: 10px;
}

.details-image img {
    width: 100%;
    height: 100%;
}

.details-image-content {
    width: 60%;
    padding: 10px;
}

.details-image-content .name {
    font-size: 12px;
    color: #666;
    border-bottom: 1px solid #666;
}

.details-image-content h3 {
    margin: 10px 0px;
    font-weight: 100;
}

.details-image-content p {
    color: #8f8f8f;
    font-size: 13px;
    display: flex;
    align-items: center;
}

.details-image-content p svg {
    margin-right: 5px;
}


.details-image-content h4 span {
    font-size: 14px;
    font-weight: 100;
}

.savings {
    font-size: 12px;
    color: #ba5252;
}

.details-image-content .details-button {
    display: flex;
    padding: 5px 0px 30px 0px;
}

.quantity {
    padding: 13px 16px;
    background-color: white;
    border: 1px solid #c8c8c8;
    border-radius: 3px;
    color: #666;
}

.add {
    padding: 13px 25px;
    background-color: #84c225;
    color: white;
    border: none;
    border-radius: 3px;
    border: 1px solid #84c225;
    margin: 0px 8px;
}

.save {
    padding: 13px 30px;
    background-color: white;
    border: 1px solid #c8c8c8;
    border-radius: 3px;
    color: #666;
}

.details-btn {
    padding: 10px 0px;
    display: flex;
    align-items: center;
    gap: 2%;
}

.details-btn .add {
    cursor: pointer;
}

.details-btn button {
    /* padding: 10px; */
    border-radius: 5px;
    border: 1px solid #9cc63d;
    background-color: white;
    font-weight: 400;
    font-size: 1.5rem;
    cursor: pointer;
    height: 2.5rem;
    width: 2.5rem;
    color: #9cc63d;
}

.qua-btn {
    color: #666;
    font-size: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.details-btn i {
    font-size: 20px;
    color: #9cc63d;
}

.bold-link {
    font-weight: bold;
    color: #666;
}

.details-image-content h5 {
    font-weight: 100;
    color: #444444;
    padding: 10px 0px;
}

.details-boxes .details-box {
    display: flex;
    justify-content: space-between;
    width: 70%;
    border: 1px solid #c8c8c8;
    border-radius: 5px;
    margin-bottom: 5px;
    cursor: pointer;
}

.details-box .weight {
    height: 100%;
    padding: 10px;
    display: flex;
    align-items: center;
    color: black;
    font-size: 13px;
}

.details-box-content {
    padding: 10px;
}

.details-box-content span {
    padding: 0px 10px;
}

.details-box-content.rate {
    color: black;
    font-size: 12px;
}

.details-box-icon {

    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 0px 4px 4px 0px;
}

.details-box-icon i {
    color: #84c225;
    font-size: 25px;
}


.head-tag {
    padding: 8px 0px;
    text-align: start;
    font-weight: 300;
    border-bottom: 1px solid lightgrey;
}

.div-details ul li {
    list-style-type: circle;
    padding: 5px 0px;
    font-size: 14px;
    color: #666;
    margin: 5px 25px;
}

.details-bottom-side .heading h1 {
    text-align: start !important;
    padding: 8px 0px;
    font-weight: 100;
    border-bottom: 1px solid lightgrey;
}

.details-bottom-side .heading {
    padding: 50px 0px 20px !important;
}

.clock {
    font-size: 18px;
    color: #000;
    padding: 0px 10px 0px 0px;
}

.main-date-row:first-of-type {
    padding-left: 4px;
    background-color: #9cc63d;
    border-radius: 6px;
    color: #fff !important;
    padding: 5px 15px;
}

div.main-date-row {
    color: #666 !important;
}

.buy-date-day {
    padding: 2px 0px;
    font-size: 12px;
    font-family: 'Roboto';
    font-weight: 500;
    margin: 0;
}

.buy-date-date {
    padding: 2px 0px;
    font-size: 16px;
    font-family: 'Roboto';
    font-weight: 500;
    margin: 0;
}

.buy-date-month {
    padding: 2px 0px;
    font-size: 12px;
    font-family: 'Roboto';
    font-weight: 500;
    margin: 0;
}

.date-row {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 10px;
}

.cureent-date {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}


/* responsive */

@media only screen and (min-width:320px),
(min-width:200px) {
    .details {
        display: flex;
        flex-direction: column;
    }

    .details-image,
    .details-image-content {
        width: 100% !important;
    }

    .quantity,
    .save {
        font-size: 7px;
    }

    .add {
        padding: 12px 18px;
        font-size: 9px;
    }

    .details-boxes .details-box {
        width: 100%;
    }

    .weight,
    .rate,
    .strike,
    .savings,
    .details-rates {
        font-size: 10px !important;
    }

    .details-box-icon i {
        font-size: 13px;
    }

    .details-box-content {
        padding: 10px 2px;
    }
}

@media only screen and (min-width:768px) {
    .details {
        display: flex;
        flex-direction: column;
    }

    .details-image,
    .details-image-content {
        width: 100% !important;
    }

    .quantity,
    .save {
        font-size: 14px;
    }

    .add {
        padding: 13px 25px;
        font-size: 14px;
    }

    .details-boxes .details-box {
        width: 70%;
    }

    .weight,
    .rate,
    .strike,
    .savings,
    .details-rates {
        font-size: 14px !important;
    }

    .details-box-icon i {
        font-size: 25px;
    }

    .details-box-content {
        padding: 10px;
    }
}

@media only screen and (min-width:1024px) ,(min-width:900px){
    .details {
        display: flex;
        flex-direction: row;
    }


    .details-image-content {
        width: 60% !important;
    }

    .details-image {
        width: 40% !important;
    }

    .quantity,
    .save {
        font-size: 14px;
    }

    .add {
        padding: 13px 25px;
        font-size: 14px;
    }

    .details-boxes .details-box {
        width: 70%;
    }

    .weight,
    .rate,
    .strike,
    .savings,
    .details-rates {
        font-size: 14px !important;
    }

    .details-box-icon i {
        font-size: 25px;
    }

    .details-box-content {
        padding: 10px;
    }
}