.anim_success {
    padding: 300px 0px;
    width: 100%;
    margin: auto;
    justify-content: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(rgba(70, 69, 69, 0.5), rgb(70, 69, 69, 0.5)), url("https://c4.wallpaperflare.com/wallpaper/642/840/7/wheat-greens-lemon-mushrooms-wallpaper-preview.jpg");
    gap: 20px;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    background-size: 100% 100%;

}

.btnnn {
    padding: 15px;
    font-size: 20px;
}

.paraaa {
    text-align: center;
    margin: 0;
    padding: 15px;
    font-size: 20px;
    color: #84c225;
    font-weight: bold;
}

.customer-details {
    box-shadow: 2px 2px 10px #ccc;
    background-color: #fff;
    padding: 10px;
    border-radius: 5px;
    margin: 10px 0px;
}

.customer-details h2 {
    color: #131411;
    margin: 10px 0px;
}

