.content-container {
    padding: 50px 0px;
}

.basket-container {
    padding: 10px 0px;
}

.card-container .cards {
    padding: 10px;
}

.card-container .cards:hover {
    box-shadow: 2px 2px 10px 3px #ccc;
    border-radius: 5px;
}

.card-container .card {
    position: relative;
}

.card-container .card-details {
    padding: 10px;
}

.card-details span {
    font-size: 11px;
    color: grey;
    padding-right: 5px;
}

.card-details .card-content {
    width: 100%;
    display: flex;
}

.card-content .card-info {
    padding: 0px 5px;
}

.card-content p {
    font-size: 10px;
    color: grey;
}

.card-button input:focus {
    outline: none;
}

.card-content p span {
    padding-right: 0px;
    font-size: 11px;
}



.card-actions {
    background-color: #f4f3f2;
}

.card-actions p {
    margin: 0;
}

.card-actions .card-button {
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-button .decrement-button {
    font-size: 10px !important;
    padding: 1px !important;
    min-width: 30px !important;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    box-shadow: 0px 0px 1px grey;
    color: grey;
    border: none;
}

.card-button input {
    width: 45px;
    padding: 5px;
    height: 21px;
    text-align: center;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border: 1px solid #ccc;
}

.card-button .increment-button {
    font-size: 12px !important;
    padding: 0px 15px !important;
    min-width: 30px !important;
    margin-left: 10px;
    background-color: #fee67c;
    border: 1px solid #fee67c;
    border: none;
    color: grey;
    display: flex;
    align-items: center;
}

.card-button .increment-button:hover {
    background-color: #f5e499;
    border: 1px solid #f5e499;
    border: none;
}

.card-button .increment-button i {
    padding-left: 5px;
}


.add-to-card-button {
    padding: 10px;
}

.add-to-card-button div {
    padding: 5px;
    width: 80%;
    background-color: #fee67c;
    border-radius: 5px;
    position: relative;
    border: 1px solid #dedede;
}

.add-to-card-button div p {
    font-size: 10px;
    text-align: center;
}

.add-to-card-button div button {
    font-weight: bold;
    font-size: 16px;
    border: none;
    padding: 1px 6px;
    color: #999999;
}

.add-button {
    position: absolute;
    top: 0;
    right: 0;
    border-left: 1px solid #dedede;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    cursor: pointer;
}

.minus-button {
    position: absolute;
    top: 0;
    left: 0;
    border-right: 1px solid #dedede;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    cursor: pointer;
}


.heading {
    padding: 50px 10px 20px 10px;
}

.heading h2 {
    padding: 8px 0px;
    text-align: center;
    font-weight: 100;
    border-bottom: 1px solid lightgrey;
}

.card-offer {
    width: 100%;
    padding: 10px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
}

.card-offer .offer {
    width: 100%;
    padding: 5px;
    box-shadow: 0px 0px 3px lightgrey;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.card-offer .offer span {
    font-size: 11px;
    color: #ba5253;
}

.card-offer .offer i {
    font-size: 11px;
    padding-left: 5px;
    color: #e1565a;
}


