.basket-container .slick-next:before {
    content: '→';
    font-size: 30px;
    font-weight: 100;
    padding: 5px;
    color: #a6a6a6;
    background-color: white;
    border: 1px solid lightgray;
    border-radius: 5px;
}

.basket-container .slick-prev:before {
    content: '←';
    font-size: 30px;
    font-weight: 100;
    padding: 5px;
    color: #a6a6a6;
    background-color: white;
    border: 1px solid lightgray;
    border-radius: 5px;
}

.basket-container .slick-prev {
    left: -1.5% !important;
    z-index: 2;
    top: 50%;
}

.basket-container .slick-next {
    right: 0 !important;
    z-index: 2;
    top: 51%;
}

.basket-container .slick-slider {
    width: 100%;
}

