.footer {
    width: 85%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    padding: 60px 0px 0px 0px;
    font-family: "Roboto";
}

.footer-card {
    width: 23%;
    padding: 10px;
}

.footer-card h4 {
    color: #84c225;
    padding: 15px 0px;
    font-weight: 100;
    margin: 0;
    font-size:16px;
}

.footer-card ul {
    line-height: 2.5;
    padding: 0;
    margin: 0;
}

.footer-card ul li {
    list-style-type: none;
    font-size: 14px;
    color: grey;
    transition: all 0.5s linear;
    cursor: pointer;
}

.footer-card ul li:hover {
    color: #84c225;
}

.footer-card ul li img {
    width: 55%;
}

.footer-icons {
    display: flex;
    gap: 2%;
}

.footer-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: white;
    border: 2px solid #ddd;
    cursor: pointer;
}

.facebook {
    background-color: #3b5998;

}

.facebook:hover {
    color: #3b5998;
    background-color: white;
    border: 2px solid #3b5998;
}

.pinterest {
    background-color: #db3236;
}

.pinterest:hover {
    color: #db3236;
    background-color: white;
    border: 2px solid #db3236;
}

.twitter {
    background-color: #00aced;
}

.twitter:hover {
    color: #00aced;
    background-color: white;
    border: 2px solid #00aced;
}

.instagram {
    background-color: #517fa4;
}

.instagram:hover {
    color: #517fa4;
    background-color: white;
    border: 2px solid #517fa4;
}



/* responsive */

@media only screen and (min-width:320px),
(min-width:200px) {

    .footer-card {
        width: 100%;
        padding: 10px;
    }
}

@media only screen and (min-width:768px) {

    .footer-card {
        width: 50%;
        padding: 10px;
    }
}

@media only screen and (min-width:1024px),(min-width:900px) {

    .footer-card {
        width: 25%;
        padding: 10px;
    }
}