.all-products-container {
    width: 85%;
    margin: auto;
    padding: 150px 0px 20px 0px;
}

.all-products-container .all-products {
    display: flex;
}

.all-products-filter {
    width: 20%;
    padding: 20px;
}

.product-filter-card {
    padding: 10px 0px;
}

.product-filter {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #dfdfdf;
}

.product-filter span {

    border-bottom: 3px solid #84c225;
}


/* .all-products-content .css-11lq3yg-MuiGrid-root {
    gap: 30px;
} */

.product-filter-content {
    padding: 10px 0px;
}

.product-search-content {
    max-height: 150px;
    overflow-y: auto;
}

.product-filter-content div {
    padding: 2px 0px;
}

.product-filter-content div label {
    padding-left: 10px;
    color: #666;
    font-size: 14px;
}


.product-search {
    position: relative;
}

.product-search input {
    width: 100%;
    height: 25px;
    padding: 10px;
    border-radius: 0px !important;
    border: 1px solid #666;
}

.product-search input:focus {
    outline: none;
}

.product-search div {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 0px 5px;
}

.product-search div i {
    font-size: 12px;
    font-weight: bold;
}


.all-products-cards {
    width: 80%;
    padding: 20px;
    border-left: 1px solid #dfdfdf;
}

.all-products-heading {
    padding: 10px 0px;
    display: flex;
    justify-content: space-between;
}

.all-products-heading h2 {
    font-weight: 100;
}

.all-products-heading select {
    width: 25%;
    font-size: 13px;
    border: 1px solid lightgrey;
    padding: 10px;
}

.card {
    margin: 10px 0px;
}

.all-products-icon {
    display: flex;
    border-bottom: 1px solid #dfdfdf;
    padding-top: 30px;
}

.all-products-icon div {
    display: flex;
    align-items: center;
    border-bottom: 3px solid #84c225;
}

.all-products-icon div span {
    font-size: 13px;
    padding-left: 5px;
}

.all-products-icon div svg {
    color: #84c225 !important;
}


.all-products-container .all-product-details {
    width: 100%;
}

.all-products-container .css-1oqqzyl-MuiContainer-root {
    padding: 0 !important;
}


.all-products-container .card-container .cards {
    padding: 0 !important;
    margin: auto;
}


.all-products-container .all-products-content {
    padding-top: 30px;
}

.all-products-content .css-mi8iqa-MuiGrid-root {
    max-width: calc(50% + 16px) !important;
}

/* responsive */

@media only screen and (min-width:320px),
(min-width:200px) {

    .all-products-filter {
        width: 100%;
    }

    .all-products-cards {
        width: 100%;
    }

    .all-products-container .all-products {
        display: flex;
        flex-direction: column;
    }

    .all-products-content .css-mi8iqa-MuiGrid-root,.css-f2t94g {
        max-width: calc(90% + 16px) !important;
    }

    .all-products-heading {
        padding: 10px 0px;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }

    .all-products-heading select {
        width: 100%;
        margin: 10px 0px;
    }
}

@media only screen and (min-width:768px) {

    .all-products-filter {
        width: 100%;
    }

    .all-products-cards {
        width: 100%;
    }

    .all-products-container .all-products {
        display: flex;
        flex-direction: column;
    }

    .all-products-content .css-mi8iqa-MuiGrid-root ,.css-f2t94g{
        max-width: calc(50% + 16px) !important;
    }

    .all-products-heading {
        padding: 10px 0px;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
    }

    .all-products-heading select {
        width: 25%;
        margin: 0px 0px;
    }
}

@media only screen and (min-width:1024px) ,(min-width:900px){

    .all-products-filter {
        width: 20%;
    }

    .all-products-cards {
        width: 80%;
    }

    .all-products-container .all-products {
        display: flex;
        flex-direction: row;
    }

    .all-products-content .css-mi8iqa-MuiGrid-root ,.css-f2t94g{
        max-width: calc(25% + 16px) !important;
    }

    .all-products-heading {
        padding: 10px 0px;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
    }

    .all-products-heading select {
        width: 25%;
        margin: 0px 0px;
    }
}